import setStyleOverride from './setStyleOverride';
import CUSTOM_EVENTS from 'Map/utils/events/customEvents';

const tranformEventName = (eventName) => eventName === 'load' ? CUSTOM_EVENTS.MAP_LOAD : eventName;

export const overriddenMethods = {
    setStyle: setStyleOverride.setStyle,
    once(eventName, callback) {
        this.__om.once(tranformEventName(eventName), callback);
    },
    on(eventName, callback) {
        this.__om.on(tranformEventName(eventName), callback);
    },
    off(eventName, callback) {
        this.__om.off(tranformEventName(eventName), callback);
    }
};



